.app {
  text-align: center;
}

.app-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #222;
  color: white;
}

.content {
  margin-top: 60px;
  height: calc(100vh - 60px);
  overflow-x: auto;
}

.search__container {
  padding: 0 10%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.search__input {
  height: 30px;
  width: 100%;
  padding: 0 10px;
  border: none;
}

.search__button {
  height: 30px;
  width: 100px;
  font-weight: bold;
  background-color: darkcyan;
  color: white;
  margin-left: 15px;
  outline: none;
  border: none;
}

.search__button:hover {
  cursor: pointer;
}

@media all and (max-width: 1050px) {
  .search__container {
    padding: 0 20px;
  }
}



